.test-page {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 100px var(--space-4) var(--space-6);
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--color-gray-50);
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}

.test-page::before {
  content: '';
  position: absolute;
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(0, 122, 255, 0.05) 0%, transparent 70%);
  border-radius: 50%;
  top: -250px;
  right: -200px;
  animation: float 20s ease-in-out infinite;
  pointer-events: none;
}

.test-page::after {
  content: '';
  position: absolute;
  width: 450px;
  height: 450px;
  background: radial-gradient(circle, rgba(255, 149, 0, 0.04) 0%, transparent 70%);
  border-radius: 50%;
  bottom: -180px;
  left: -150px;
  animation: float 26s ease-in-out infinite reverse;
  pointer-events: none;
}

.page-bg-wrapper {
  display: none !important;
}

.question-count {
  position: relative;
  z-index: 1;
  width: 100%;
  text-align: center;
  font-size: 0.938rem;
  color: #515154;
  margin-bottom: var(--space-4);
  font-weight: 600;
  letter-spacing: -0.01em;
}

.flashcard {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: 650px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: var(--radius-xl);
  padding: var(--space-6);
  margin: 0 auto var(--space-5);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.6);
}

.question {
  width: 100%;
  font-size: 1.25rem;
  color: #000000;
  margin-bottom: var(--space-6);
  text-align: center;
  line-height: 1.5;
  font-weight: 700;
  letter-spacing: -0.01em;
}

.answers {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--space-3);
  margin: 0 auto;
}

.modern-btn {
  width: 100%;
  padding: var(--space-4) var(--space-5);
  text-align: left;
  background: var(--color-white);
  border: 2px solid var(--color-gray-300);
  border-radius: var(--radius-md);
  font-size: 1rem;
  color: #1D1D1F;
  cursor: pointer;
  transition: all var(--transition-base);
  margin: 0 auto;
  font-weight: 600;
  letter-spacing: -0.01em;
}

.modern-btn:hover {
  background: var(--color-gray-50);
  border-color: var(--color-gray-400);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.modern-btn.selected {
  background: var(--color-blue);
  border-color: var(--color-blue);
  color: var(--color-white);
  box-shadow: var(--shadow-md);
}

/* Make TestPage buttons take precedence over global .modern-btn gradients/focus */
.test-page .modern-btn,
.test-page .answers button.modern-btn {
  background: var(--color-white) !important;
  color: #1D1D1F !important;
  border: 2px solid var(--color-gray-300) !important;
  box-shadow: none !important;
  font-weight: 600 !important;
}

.test-page .modern-btn:focus,
.test-page .modern-btn:active {
  outline: none !important;
  box-shadow: 0 0 0 4px rgba(0, 122, 255, 0.1) !important;
}

.test-page .modern-btn.selected {
  background: var(--color-blue) !important;
  border-color: var(--color-blue) !important;
  color: var(--color-white) !important;
  box-shadow: var(--shadow-md) !important;
}

.test-nav {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: var(--space-4);
}

/* Results page styling */
.header {
  width: 100%;
  text-align: center;
  margin-bottom: var(--space-8);
}

.header h2 {
  font-size: 2rem;
  color: #000000;
  margin-bottom: var(--space-2);
  font-weight: 700;
  letter-spacing: -0.02em;
}

.header-accent {
  color: var(--color-blue);
}

.test-stats-box {
  width: 100%;
  max-width: 700px;
  margin: 0 auto var(--space-6);
  padding: var(--space-6);
  background: var(--color-white);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--color-gray-200);
}

.test-stats-box > div {
  padding: var(--space-3) 0;
  border-bottom: 1px solid var(--color-gray-200);
  font-size: 1rem;
  color: #1D1D1F;
  font-weight: 600;
}

.test-stats-box > div:last-child {
  border-bottom: none;
}

.results-grid {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
}

.results-grid > b {
  display: block;
  margin: var(--space-6) 0 var(--space-4);
  color: #000000;
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: -0.01em;
}

.question-box {
  background: var(--color-white);
  border-radius: var(--radius-md);
  padding: var(--space-5);
  margin-bottom: var(--space-3);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--color-gray-200);
}

.q-title {
  font-weight: 700;
  color: #000000;
  margin-bottom: var(--space-3);
  font-size: 1rem;
  line-height: 1.5;
}

.wrong {
  color: #FF3B30;
  font-weight: 500;
}

.correct {
  color: #34C759;
  font-weight: 500;
}

.all-correct {
  text-align: center;
  padding: var(--space-5);
  color: #34C759;
  font-size: 1.125rem;
  font-weight: 500;
}

@media (max-width: 768px) {
  .test-page {
    padding: 15px;
  }

  .flashcard {
    width: 95%;
    padding: 20px;
  }

  .question {
    font-size: 1.2rem;
  }

  .modern-btn {
    padding: 12px 20px;
    font-size: 1rem;
  }
}
